import { defineComponent, reactive } from "vue";
import EventEmitter from "@/util/EventEmitter";
import TextProperty from "@/components/TextProperty/index.vue";
import useVuelidate from "@vuelidate/core";
import { required, email } from "@vuelidate/validators";
import HttpClient from "@/util/HttpClient";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import { functionAuthGuard, getInstance } from "@/auth/Auth";

const context: unknown = {};
const EMPTY_VALUES = [null, undefined, ""];

export default defineComponent({
  name: "BlockPropertiesComponent",
  props: {
    parameters: [],
    blockId: String,
    ctx: Object,
    show: Boolean,
    botContext: [],
    uid: Number,
    nuid: String,
    blockType: String,
    refresh: Number,
    authToken: String,
    blockTypeId: Number,
  },
  setup(props, { expose }) {
    const tagifyStuff = reactive({
      value: "",
      tagifySettings: {
        autoComplete: {
          enabled: true,
        },
        duplicates: true,
        tagTextProp: "value",
        pattern: "#",
        mode: "mix",
        enforceWhitelist: true,
        mixMode: {
          insertAfterTag: " ",
        },
        backspace: true,
        editTags: false,
        templates: {
          dropdownItem,
        },
        transformTag,
        whitelist: [],
        dropdown: {
          enabled: 0,
          maxItems: 100,
          position: "text",
          mapValueTo: "value",
          highlightFirst: true,
          placeAbove: true,
        },
      },
    });

    function dropdownItem(tagData: any) {
      return `<div class="tagify__dropdown__item">
        <i class="fas fa-database"></i>
        <span class="ml-2">${tagData.value}</span>
      </div>`;
    }

    function transformTag(tagData: any) {
      tagData.style = "--tag-bg: #aee8be";
      // Requested by Backend (?)
      if (tagData.type === "NUM" && !tagData.decimals) tagData.decimals = "";
    }

    return {
      tagifyStuff,
      v$: useVuelidate(),
    };
  },
  created() {
    this.resetEventListeners();
    this.tagifyStuff.tagifySettings.whitelist = [];
    this.context = {};
    this.form = {};
    this.validations = {};
  },
  validations() {
    return this.validations;
  },
  data() {
    return {
      context,
      form: {} as any,
      validations: {},
      refreshingSelectOption: false,
      refreshedParameters: {} as any,
    };
  },
  components: {
    TextProperty,
  },
  watch: {
    botContext: {
      handler(options) {
        // removeTags from white list
        if (options.length && options.length > 0) {
          this.tagifyStuff.tagifySettings.whitelist = options?.map(function (
            item: any
          ) {
            return typeof item == "string" ? { value: item } : item;
          });
        } else {
          this.tagifyStuff.tagifySettings.whitelist = [];
        }
      },
    },
    parameters: {
      handler(params) {
        if (params && params.length !== 0) {
          const validations: any = {
            form: {},
          };
          const params: any = this.$props.parameters;
          for (const param of params) {
            // TODO: workaround while solving an issue in the backend
            if (param.mandatory && param.type !== "BOO") {
              validations["form"][param.slug] = { required };
            }
            if (param.type === "LIS") {
              if (EMPTY_VALUES.includes(this.form[param.slug])) {
                this.form[param.slug] =
                  this.form[param.slug] || param.placeholder;
                if (param.add_url) this.form[param.slug] = null;
              }
              if (param.add_url && this.authToken) this.refreshList(param.slug); // Don't wait, async list population
            }
            EventEmitter.emitter.on(
              `block-properties-remove-tag-from-field-${this.$props.blockId}`,
              (block: string, data: any) => {
                this.removeTagValueFromField(
                  data.id.substring(0, data.id.indexOf("-")),
                  data.value
                );
              }
            );
            EventEmitter.emitter.emit(
              "block-properties-change",
              this.$props.blockId,
              {
                field: param.slug,
                value: this.form[param.slug],
                error: false,
                params,
                blockUuid: this.nuid,
                blockType: this.blockType,
              }
            );
          }
          this.validations = validations;
          setTimeout(this.sendErrorStatus, 400);
        }
      },
    },
    ctx: {
      handler(ctx) {
        this.form = ctx;
      },
    },
  },
  methods: {
    authGuard: functionAuthGuard,
    addParameter(url: string) {
      const w = 800;
      const h = 800;
      const left = (screen.width - w) / 2;
      const top = (screen.height - h) / 4;
      window.open(
        url,
        "windowName",
        `popup=yes;toolbar=no,scrollbars=no,resizable=yes,top=${top},left=${left},width=${w},height=${h}`
      );
    },
    formatUrl(url: string) {
      const u = url.replaceAll("&amp;", "&");
      return `${u}&state=from_editor`;
    },
    resetEventListeners() {
      EventEmitter.emitter.off(`get-auth-token-response`);
      EventEmitter.emitter.off(
        `block-properties-remove-tag-from-field-${this.$props.blockId}`
      );
    },
    async refreshList(slug: string) {
      this.refreshingSelectOption = true;
      const response = await this.refreshSelectOptions(slug);
      // Weird endpoint response from backend (?), need to find the parameter in it
      const parameter = this.getValuesFromParameterListBySlug(
        response[0].parameters,
        slug
      );
      this.refreshedParameters[slug] = parameter;
      this.refreshingSelectOption = false;
    },
    async refreshSelectOptions(slug: string) {
      const url = `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/refresh_param`;
      const body: any = {
        block_id: this.blockTypeId,
        param_slug: slug,
      };
      return await HttpClient.request(
        url,
        this.authToken!,
        HttpRequestMethod.POST,
        JSON.stringify(body)
      );
    },

    getValuesFromParameterListBySlug(parameters: Array<any>, slug: string) {
      const foundValues: any = parameters!.find((o: any) => o.slug === slug);
      return foundValues;
    },
    removeTagValueFromField(field: string, value: string) {
      const regex = `\\[\\[${value.substring(2, value.indexOf('",'))}.+?\\]\\]`;
      const r = new RegExp(regex, "g");
      if (this.form[field]) {
        const st = this.form[field].indexOf(
          value.substring(0, value.indexOf('",'))
        );
        if (st > -1) {
          this.form[field] = this.form[field].replace(r, "");
        }
      }
      this.v$.$touch();
      this.validateField(field, this.form[field]);
    },
    validateField(field: string, value: string) {
      EventEmitter.emitter.emit(
        "block-properties-change",
        this.$props.blockId,
        {
          field,
          value,
          error: this.v$.$error,
          params: this.$props.parameters,
          blockUuid: this.nuid,
          blockType: this.blockType,
        }
      );
      this.sendErrorStatus();
    },
    getTagMatchesForField(field: string) {
      const regex = /\[\[\{"value":"(.*?)","text":/gm;
      const result = [];
      let m;
      while ((m = regex.exec(field)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (m.index === regex.lastIndex) {
          regex.lastIndex++;
        }
        result.push({
          value: m[0],
          tag: m[1],
        });
      }
      return result;
    },
    updateField(field: number, blockId: string, value: string) {
      if (blockId === this.blockId) {
        this.form[field] = value;
        EventEmitter.emitter.emit(
          "block-properties-change",
          this.$props.blockId,
          {
            field,
            value,
            error: this.v$.$error,
            params: this.$props.parameters,
            blockUuid: this.nuid,
            blockType: this.blockType,
          }
        );
        EventEmitter.emitter.emit(
          "block-properties-tag-removal",
          this.$props.blockId
        );
      }
    },
    sendErrorStatus() {
      this.v$.$touch();
      EventEmitter.emitter.emit(
        `block-properties-error-${this.nuid}`,
        this.blockId,
        this.v$.$error
      );
    },
    change(ev: any) {
      let value = "";
      let id = "";
      if (ev.target) {
        value =
          ev.target.type === "checkbox" ? ev.target.checked : ev.target.value;
        id = ev.target.id;
      } else {
        value = ev.value;
        id = ev.id;
      }
      id = id.substring(0, id.indexOf("-"));
      this.form[id] = value;
      this.validateField(id, value);
    },
  },
});
